.recipe{
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgb(71,71,71);
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background-color: white;
    align-items: center;
    min-width: 40%;
}
.title{
    display: flex;
    flex-wrap: wrap;
}

.image{
    margin-top: 20px;
    border-radius: 20%;
    width: 200px;
    height: 200px;
    object-fit: contain;
}